export default {
  name: "Activity",
  // layout: 'single-container',
  data() {
    return {
      activityList: [
        {sCoverImg: require('@/assets/img/activity/activity_making.png'), iType: 1},
        {sCoverImg: require('@/assets/img/activity/activity_making.png'), iType: 1},
        {sCoverImg: require('@/assets/img/activity/activity_making.png'), iType: 1},
      ]
    }
  },

  computed:{

  },

  mounted(){
    this.getList();
  },
  methods:{
    checkRed(v){
      let ary = [];
      let temp = localStorage.getItem('ActivityList');
      if(temp){
          ary = JSON.parse(temp);
          if(ary.indexOf(v.iListId) >-1){
            return false;
          }
      }
      return true;
    },
    async getList(){
      let {vActivity} = await this.$axios.post('v1/emall/GetActivityList',{iPageNo:0,iPageNum:100});
      if(vActivity.length){
        this.activityList = vActivity;
      }
    },
    toActivity(item){
      this.$store.commit('CHANGE_DATA',{key:'activityDetail',val:item});
      this.$nextTick(()=>{
        this.$router.push({path:'/activity-detail'})
      })
    }
  }
}
